
    <template>
      <section class="market-doc-container">
        <h2>dropdown 下拉菜单</h2>
<h3>基本使用</h3>
<p>一般使用mku-dropdown-menu作为下拉菜单的内容。</p>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-dropdown @click=&quot;handleClick&quot;&gt;
      &lt;mku-button type=&quot;primary&quot;&gt;下拉菜单&lt;/mku-button&gt;
      &lt;mku-dropdown-menu slot=&quot;menu&quot;&gt;
        &lt;mku-dropdown-item name=&quot;a1&quot;&gt;披萨&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a2&quot;&gt;汉堡&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a3&quot;&gt;新疆羊肉串&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a4&quot;&gt;意大利面&lt;/mku-dropdown-item&gt;
      &lt;/mku-dropdown-menu&gt;
    &lt;/mku-dropdown&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    handleClick (name) {
      this.$message.success(`点击了：${name}`)
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>触发条件</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-dropdown trigger=&quot;hover&quot;&gt;
      &lt;mku-button type=&quot;primary&quot;&gt;hover触发&lt;/mku-button&gt;
      &lt;mku-dropdown-menu slot=&quot;menu&quot;&gt;
        &lt;mku-dropdown-item&gt;蒙牛早餐奶&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;伊利纯牛奶&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;果粒橙&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;可口可乐&lt;/mku-dropdown-item&gt;
      &lt;/mku-dropdown-menu&gt;
    &lt;/mku-dropdown&gt;

    &lt;mku-dropdown trigger=&quot;click&quot;&gt;
      &lt;mku-button type=&quot;primary&quot;&gt;click触发&lt;/mku-button&gt;
      &lt;mku-dropdown-menu slot=&quot;menu&quot;&gt;
        &lt;mku-dropdown-item name=&quot;a1&quot;&gt;披萨&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a2&quot;&gt;汉堡&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a3&quot;&gt;新疆羊肉串&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item name=&quot;a4&quot;&gt;意大利面&lt;/mku-dropdown-item&gt;
      &lt;/mku-dropdown-menu&gt;
    &lt;/mku-dropdown&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>禁用项</h3>
<p>mku-dropdown-menu菜单项支持通过disabled属性设置禁用项。</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-dropdown&gt;
      &lt;mku-button type=&quot;primary&quot;&gt;下拉菜单&lt;/mku-button&gt;
      &lt;mku-dropdown-menu slot=&quot;menu&quot;&gt;
        &lt;mku-dropdown-item disabled&gt;禁用项目1&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;橘子&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item disabled&gt;禁用项目2&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;苹果&lt;/mku-dropdown-item&gt;
      &lt;/mku-dropdown-menu&gt;
    &lt;/mku-dropdown&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>选中项</h3>
<p>mku-dropdown-menu菜单项支持通过active属性设置禁用项。</p>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-dropdown&gt;
      &lt;mku-button type=&quot;primary&quot;&gt;下拉菜单&lt;/mku-button&gt;
      &lt;mku-dropdown-menu slot=&quot;menu&quot;&gt;
        &lt;mku-dropdown-item active&gt;凤梨&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;橘子&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item active&gt;菠萝&lt;/mku-dropdown-item&gt;
        &lt;mku-dropdown-item&gt;苹果&lt;/mku-dropdown-item&gt;
      &lt;/mku-dropdown-menu&gt;
    &lt;/mku-dropdown&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>Dropdown Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">trigger</td>
<td style="text-align:left">触发方式</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>click | hover</code></td>
<td style="text-align:left"><code>click</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">placement</td>
<td style="text-align:left">dropdown显示的位置</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">同<code>popper.js</code></td>
<td style="text-align:left"><code>bottom-start</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Dropdown Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">click</td>
<td style="text-align:left">点击dropdown-item时触发</td>
<td style="text-align:left">当前dropdown-item的name值</td>
</tr>
<tr>
<td style="text-align:left">visible-change</td>
<td style="text-align:left">dropdown展开收起状态变化时触发</td>
<td style="text-align:left">当前状态</td>
</tr>
<tr>
<td style="text-align:left">click-aside</td>
<td style="text-align:left">点击空白区域关闭dropdown时触发</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
<h3>Dropdown Slots</h3>
<table>
<thead>
<tr>
<th style="text-align:left">name</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">menu</td>
<td style="text-align:left">dropdown的下拉内容，一般使用dropdown-menu组件</td>
</tr>
</tbody>
</table>
<h3>Dropdown—Item Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">name</td>
<td style="text-align:left">当前项的值</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disbaled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">active</td>
<td style="text-align:left">是否选中</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>

      </section>
    </template>
